.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.container {
  max-width: 1320px;
  margin: 0 auto;
  padding-left: 25px;
  padding-right: 25px;
}
@media (min-width: 768px) {
  .container {
    padding-left: 40px;
    padding-right: 40px;
  }
}
@media (min-width: 1440px) {
  .container {
    padding-left: 60px;
    padding-right: 60px;
  }
}
.transport {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}
.transport__item {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 32px;
  height: 32px;
  position: relative;
  text-decoration: none;
  border-radius: 50%;
  background-repeat: no-repeat;
  background-position: 50%;
  cursor: help;
}
.transport__item .transport__icon {
  fill: #D9D9D9;
}
.transport__item--active .transport__icon {
  fill: #1D2E5B;
}
.transport__tooltip {
  display: none;
  padding: 15px 20px;
  position: absolute;
  top: 45px;
  left: 50%;
  transform: translate(-50%, 0);
  font-size: 20px;
  line-height: 20px;
  color: #FFFFFF;
  text-align: center;
  background-color: #101D41;
  border-radius: 4px;
}
.transport__tooltip::before {
  content: "";
  position: absolute;
  top: -14px;
  left: 50%;
  transform: translate(-50%, 0);
  border: 7px solid transparent;
  border-bottom: 8px solid #101D41;
}
.transport__item:hover .transport__tooltip {
  display: block;
  z-index: 10;
}
.checkbox__label {
  display: block;
  position: relative;
  font-size: 15px;
  line-height: 15px;
  cursor: pointer;
  user-select: none;
}
@media (min-width: 768px) {
  .checkbox__label {
    font-size: 20px;
    line-height: 20px;
    font-weight: 500;
    text-transform: uppercase;
    letter-spacing: 0.1em;
  }
}
.checkbox__label--new-plan {
  margin-bottom: 18px;
  padding: 10px 40px;
}
@media (min-width: 768px) {
  .checkbox__label--new-plan {
    margin-bottom: 22px;
    padding: 10px 40px 10px 58px;
  }
}
@media (min-width: 1440px) {
  .checkbox__label--new-plan {
    margin-bottom: 30px;
    padding: 10px 0 10px 58px;
  }
}
.checkbox__label--preferences {
  line-height: 20px;
  color: #000000;
}
@media (min-width: 768px) {
  .checkbox__label--preferences {
    font-weight: 400;
    text-transform: none;
    letter-spacing: normal;
  }
}
.checkbox__input + .checkbox__label--preferences:hover::before,
.checkbox__input:focus + .checkbox__label--preferences::before {
  opacity: 60%;
}
.checkbox__input + .checkbox__label--preferences:active::before {
  opacity: 30%;
}
.checkbox__input + .checkbox__label--preferences:active::after {
  opacity: 20%;
}
.checkbox__label--transport {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 32px;
  height: 32px;
  border-radius: 50%;
  background-color: #FFFFFF;
  text-transform: none;
}
@media (min-width: 768px) {
  .checkbox__label--transport {
    width: 43px;
    height: 43px;
  }
}
.checkbox__label--transport:hover,
.checkbox__input:focus + .checkbox__label--transport {
  box-shadow: 0 2px 6px rgba(22, 28, 53, 0.17);
}
.checkbox__label--transport:active {
  box-shadow: none;
  background-color: #E2E5F1;
}
.checkbox__transport-icon {
  width: 17px;
  height: auto;
  fill: #D9D9D9;
}
@media (min-width: 768px) {
  .checkbox__transport-icon {
    width: 21px;
    height: auto;
  }
}
.checkbox__tooltip {
  top: 55px;
  cursor: default;
}
.checkbox__label--transport:hover .checkbox__transport-icon {
  fill: #B2B2B2;
}
.checkbox__label--transport:active .checkbox__transport-icon {
  fill: #1D2E5B;
  opacity: 10%;
}
.checkbox__input:checked + .checkbox__label--transport .checkbox__transport-icon {
  fill: #1D2E5B;
}
.checkbox__input:checked + .checkbox__label--transport:hover .checkbox__transport-icon {
  opacity: 70%;
}
.checkbox__input:checked + .checkbox__label--transport:active .checkbox__transport-icon {
  opacity: 30%;
}
.checkbox__input + .checkbox__label::before {
  content: "";
  position: absolute;
  top: 0;
  left: -26px;
  width: 18px;
  height: 18px;
  background-color: #FFFFFF;
  border-radius: 4px;
}
@media (min-width: 768px) {
  .checkbox__input + .checkbox__label::before {
    width: 24px;
    height: 24px;
    top: -2px;
    left: -36px;
  }
}
.checkbox__input:checked + .checkbox__label::after {
  content: "";
  position: absolute;
  top: 4px;
  left: -21px;
  width: 9px;
  height: 9px;
  background-image: url("../img/icon-check-mark.svg");
  background-repeat: no-repeat;
  background-position: 50% 50%;
}
@media (min-width: 768px) {
  .checkbox__input:checked + .checkbox__label::after {
    width: 11px;
    height: 11px;
    top: 5px;
    left: -28px;
    background-size: 12px;
  }
}
.checkbox__input + .checkbox__label--transport::before,
.checkbox__input:checked + .checkbox__label--transport::after {
  display: none;
}
.checkbox__input + .checkbox__label--new-plan::before {
  top: 2px;
  left: 0;
  width: 26px;
  height: 26px;
  border: 1px solid #CBCED9;
  box-sizing: border-box;
}
@media (min-width: 768px) {
  .checkbox__input + .checkbox__label--new-plan::before {
    width: 34px;
    height: 34px;
  }
}
.checkbox__input:checked + .checkbox__label--new-plan::after {
  top: 11px;
  left: 9px;
}
@media (min-width: 768px) {
  .checkbox__input:checked + .checkbox__label--new-plan::after {
    top: 15px;
    left: 13px;
  }
}
.checkbox__input + .checkbox__label--new-plan:hover::before,
.checkbox__input:focus + .checkbox__label--new-plan::before {
  border: 1px solid #959BB2;
}
.checkbox__input + .checkbox__label--new-plan:active::before {
  background-color: #E2E5F1;
  border: none;
}
.checkbox__input + .checkbox__label--new-plan:active::after {
  opacity: 30%;
}
.checkbox__input:disabled + .checkbox__label {
  opacity: 15%;
}
.input {
  position: relative;
}
.input__number {
  width: 100%;
  padding: 12px 50px;
  font-family: inherit;
  font-size: 15px;
  font-weight: 500;
  color: inherit;
  text-align: center;
  border: 1px solid #CBCED9;
  box-sizing: border-box;
  border-radius: 4px;
  appearance: textfield;
  outline: none;
}
@media (min-width: 768px) {
  .input__number {
    padding: 22px 50px;
    font-size: 20px;
  }
}
@media (min-width: 1440px) {
  .input__number {
    padding: 22px 50px 23px;
  }
}
.input__number:hover {
  border: 1px solid #959BB2;
}
.input__number:hover ~ .input__button::before,
.input__number:hover ~ .input__button::after {
  background-color: #959BB2;
}
.input__number:focus {
  border: 1px solid #161C35;
}
.input__number:focus ~ .input__button::before,
.input__number:focus ~ .input__button::after {
  background-color: #161C35;
}
.input__number::-webkit-outer-spin-button,
.input__number::-webkit-inner-spin-button {
  appearance: none;
  margin: 0;
}
.input__button {
  padding: 0 18px;
  border: none;
  position: absolute;
  top: 0;
  font-family: inherit;
  height: 100%;
  cursor: pointer;
  background-color: transparent;
}
@media (min-width: 768px) {
  .input__button {
    padding: 0 30px;
  }
}
@media (min-width: 1440px) {
  .input__button {
    padding: 0 25px;
  }
}
.input__button::before,
.input__button::after {
  content: "";
  position: absolute;
  width: 14px;
  height: 2px;
  background-color: #CCCCCC;
  top: 0;
  bottom: 0;
  left: 14px;
  margin: auto;
}
@media (min-width: 768px) {
  .input__button::before,
  .input__button::after {
    width: 20px;
    left: 23px;
  }
}
@media (min-width: 1440px) {
  .input__button::before,
  .input__button::after {
    left: 18px;
  }
}
.input__button:hover::before,
.input__button:focus::before,
.input__button:hover::after,
.input__button:focus::after {
  background-color: #999999;
}
.input__button:active::before,
.input__button:active::after {
  background-color: #E5E5E5;
}
.input__button--less {
  left: 0;
}
.input__button--more {
  right: 0;
}
.input__button--more::after {
  transform: rotate(90deg);
}
.input__button--more::before,
.input__button--more::after {
  left: auto;
  right: 14px;
}
@media (min-width: 768px) {
  .input__button--more::before,
  .input__button--more::after {
    right: 23px;
  }
}
@media (min-width: 1440px) {
  .input__button--more::before,
  .input__button--more::after {
    right: 18px;
  }
}
