.profile-promo {
  margin-top: -30px;
  padding: 81px 0 36px;
  background-color: @light-blue;
  border-radius: 0 0 30px 30px;
  position: relative;
  z-index: @zindex-profile-promo;

  @media (min-width: @tablet-width) {
    margin-top: -60px;
    padding: 131px 0 62px;
    border-radius: 0 0 60px 60px;
  }

  @media (min-width: @desktop-width) {
    padding: 162px 0 164px;
  }
}

.profile-promo__heading {
  .offset();
  max-width: 200px;
  margin-bottom: 44px;
  padding: 0 100px 0 25px;
  font-size: 24px;
  line-height: 24px;
  font-weight: 700;
  color: @special-orange;

  @media (min-width: @tablet-width) {
    max-width: 100%;
    margin-bottom: 36px;
    padding: 0;
    font-size: 45px;
    line-height: 45px;
    text-align: center;
  }

  @media (min-width: @desktop-width) {
    margin-bottom: 97px;
  }
}

.profile-promo__list {
  .offset();
  margin-bottom: 27px;
  padding: 0 46px 0 24px;
  position: relative;

  @media (min-width: @tablet-width) {
    margin-bottom: 33px;
    padding: 40px 35px 0 90px;

    &::before {
      content: "";
      position: absolute;
      width: 198px;
      height: 100%;
      padding-bottom: 45px;
      background-color: @filter-blue;
      border-radius: 20px;
      top: 0;
      right: 0;
    }
  }

  @media (min-width: @desktop-width) {
    margin-bottom: 32px;
    padding-left: 161px;
    padding-right: 345px;

    &::before {
      width: 446px;
      height: 100%;
      padding-bottom: 60px;
      top: -10px;
      right: 183px;
    }
  }

  &::after {
    content: "";
    position: absolute;
    top: -128px;
    right: 0;
    width: 100px;
    height: 100px;
    background-image: url("../img/rates.png");
    background-repeat: no-repeat;
    z-index: @zindex-profile-promo;

    @media (min-resolution: 2dppx) {
      background-image: url("../img/rates@2x.png");
      background-size: 100px auto;
    }

    @media (min-width: @tablet-width) {
      top: auto;
      bottom: -120px;
      right: 45px;
    }

    @media (min-width: @desktop-width) {
      bottom: 0;
      right: 73px;
      width: 198px;
      height: 198px;
      background-image: url("../img/rates-desktop.png");

      @media (min-resolution: 2dppx) {
        background-size: 198px auto;
      }
    }
  }
}

.profile-promo__item {
  padding: 12px 0 15px;
  position: relative;
  line-height: 16px;
  font-weight: 300;
  color: @special-grey;
  border-top: 1px solid @grey-border;

  &:last-child {
    border-bottom: 1px solid @grey-border;
  }

  @media (min-width: @tablet-width) {
    display: flex;
    justify-content: space-between;
    padding: 18px 0 13px;
    font-size: 28px;
    line-height: 28px;
  }

  @media (min-width: @desktop-width) {
    margin-bottom: 0;
    padding: 18px 0 19px;
  }

  &::before {
    content: "";
    width: 34px;
    height: 34px;
    position: absolute;
    top: 2px;
    right: -45px;
    color: @basic-white;
    background-color: @special-orange;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;

    @media (min-width: @tablet-width) {
      width: 66px;
      height: 66px;
      top: 0;
      left: -89px;
    }
  }

  &:nth-child(1)::before {
    content: "1";
    background-color: @special-orange;
  }

  &:nth-child(2)::before {
    content: "2";
    background-color: @special-yellow-dark;
  }

  &:nth-child(3)::before {
    content: "3";
    background-color: @basic-yellow;
  }
}

.profile-promo__service {
  margin-bottom: 6px;

  @media (min-width: @desktop-width) {
    margin-bottom: 0;
  }
}

.profile-promo__price {
  .offset();
}

.profile-promo__business-rates {
  display: block;
  font-weight: 700;
  text-decoration: none;
  color: @blue-link;
  text-align: center;

  @media (min-width: @tablet-width) {
    padding: 25px 0 25px 89px;
    position: relative;
    font-size: 20px;
    line-height: 20px;
    text-align: left;

    &::before {
      content: "";
      width: 66px;
      height: 66px;
      position: absolute;
      top: 0;
      left: 0;
      background-color: @filter-blue;
      border-radius: 50%;
    }
  }

  @media (min-width: @desktop-width) {
    padding-left: 160px;

    &::before {
      left: 73px;
    }
  }

  &:hover,
  &:focus {
    color: fade(@blue-link, 70%);
  }

  &:active {
    color: fade(@blue-link, 30%);
  }
}

.profile-promo__icon {
  display: none;

  @media (min-width: @tablet-width) {
    display: block;
    position: absolute;
    top: 23px;
    left: 24px;
  }

  @media (min-width: @desktop-width) {
    left: 96px;
  }
}
